<template>
  <div id="app">
    <nav-bar :bodyWidth="bodyWidth"></nav-bar>
    <router-view :bodyWidth="bodyWidth"></router-view>
    <page-footer></page-footer>
    <return-top></return-top>
    <modal :centerDialogVisible="centerDialogVisible" :modalStatus="modalStatus"></modal>
  </div>
</template>
<script>
import Lodash from 'lodash';
import navBar from '@/components/navBar'; //顶部栏
import pageFooter from '@/components/pageFooter'; //页脚
import returnTop from '@/components/returnTop'; //底部浮动框
import modal from '@/components/modal'; //模态框


export default {
  name: 'App',
  data() {
    return {
      //页面宽度
      bodyWidth: document.body.clientWidth,
    }
  },
  created() {
    try {
      document.body.removeChild(document.getElementById('appLoading'))
      setTimeout(function() {
        document.getElementById('app').style.display = 'block';
      }, 500)
    } catch (e) {

    }
  },
  wacth: {
    bodyWidth(val) {
      this.bodyWidth = val;
    },
  },
  mounted() {
    //监听页面宽度
    const that = this;
    // _.debounce 是一个通过 lodash 限制操作频率的函数。
    window.onresize = _.debounce(() => {
      //console.log("onresize:" + that.bodyWidth)
      that.bodyWidth = document.body.clientWidth
    }, 400);
  },
  computed: {
    centerDialogVisible() {
      return this.$store.getters.getCenterDialogVisible;
    },
    modalStatus() {
      return this.$store.getters.getModalStatus;
    },
  },
  components: {
    navBar,
    pageFooter,
    returnTop,
    modal,
  }
}

</script>
<style lang="scss">
@import './assets/sass/public.scss';
#app {
  margin: 0;
  padding: 0;
  height: 100%;
  font-family: Helvetica Neue, Helvetica, PingFang SC, Hiragino Sans GB, Microsoft YaHei, SimSun, sans-serif;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
}

a {
  text-decoration: none;
}

a:hover {
  color: $blue;
}

a:active {
  color: $blue;
}

a:visited {
  color: #8492A6;
}

</style>



