<template>
  <div class="listBox" v-if="projectLists.length != 0">
    <div class="textBox">
      <div class="yearsTitle">{{yearsTitle}}</div>
      <div class="more" v-if="projectListsData.length > 4"><a href="javascript:;">更多 ></a></div>
    </div>
    <div class="projectList">
      <div class="projectBox" v-for="(project,index) in projectLists" @click="openProject">
        <div class="listMask"></div>
        <div class="projectImg" :style="'background-image: url(static/worksLists/'+ project.projectImg+')'"></div>
        <div class="projectTitle">{{project.projectTitle}}</div>
        <div class="projectText">{{project.projectText}}</div>
      </div>
    </div>
  </div>
</template>
<script type="text/javascript">
export default {
  //该页面的控制数据
  data() {
    return {
      projectList: [],
      projectLists: [],
      projectListsData:[],
      projectListsData2018: [{
        projectImg: 'jidong201801.png',
        projectTitle: '东风生产执行系统-Pad端',
        projectText: '广州极动零部件工厂生产用移动APP',
      }, ],
      projectListsData2017: [{
        projectImg: 'xunyi201711.png',
        projectTitle: '寻医问药APP',
        projectText: '语音挂号APP，语音操作更便捷。',
      }, {
        projectImg: 'baobei201708.png',
        projectTitle: '报贝2.0',
        projectText: '香港移动商务报销APP，让公司报销更便捷。',
      }, 
      // {
      //   projectImg: 'xiaodianlun201705.png',
      //   projectTitle: '小电轮-小程序',
      //   projectText: '共享电动车，让出行更便捷。',
      // }, 
      {
        projectImg: 'mesapp201703.png',
        projectTitle: '东风生产执行系统-APP端',
        projectText: '东风设计院装备研发中心工厂生产用移动APP',
      }, ],
      projectListsData2016: [{
        projectImg: 'waimai201707.png',
        projectTitle: '香港报贝外卖APP',
        projectText: '报贝移动报销新增业务板块',
      }, {
        projectImg: 'one2pay201709.png',
        projectTitle: '香港one2payPOS机界面设计',
        projectText: '报贝移动报销新增业务板块',
      },],
    }
  },
  //父控件传过来的参数
  props: {
    yearsTitle: {
      type: String,
      default: '',
    }
  },
  //监听函数
  wacth: {

  },
  //进入页面执行的函数
  mounted() {
    this.showProjectLists();
  },
  //定义函数
  methods: {
    //打开项目页面
    openProject() {
      console.log(this.projectList);
    },
    //将回传的projectListsData传入projectLists
    showProjectLists() {
      if (this.yearsTitle == '2018年') {
        this.projectLists = this.projectListsData2018.slice(0, 4);
        this.projectListsData = this.projectListsData2018;
      };
      if (this.yearsTitle == '2017年') {
        this.projectLists = this.projectListsData2017.slice(0, 4);
        this.projectListsData = this.projectListsData2017;
      };
      if (this.yearsTitle == '2016年') {
        this.projectLists = this.projectListsData2016.slice(0, 4);
        this.projectListsData = this.projectListsData2016;
      }
    }
  },
  //增加控件
  components: {

  }
}

</script>
<style scoped lang="scss">
@import '../assets/sass/public.scss';
.listBox {
  width: 1200px;
  margin: 0 auto; // display: inline-block;
  margin-top: 20px;
  margin-bottom: 20px;
  display: block;
}

.projectList {
  width: 1200px; //height: 258px;
  overflow: hidden;
  zoom: 1;
}

.textBox {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.yearsTitle {
  color: $bigTitle;
  font-size: $bigTitle-size;
  margin-left: 10px;
}

.more {
  font-size: 14px;
  color: $textP;
}

.more a:link {
  color: $textP;
}

.more a:hover {
  color: $blue;
}

.projectBox {
  margin: 0 10px;
  float: left;
}

.projectBox:hover .listMask {
  display: inline-block;
  transition: all 0.3s ease-out 0s;
}

.listMask {
  width: 280px;
  height: 160px;
  background: rgba(255, 255, 255, 0.2);
  margin: 20px 0;
  border-radius: 4px;
  position: absolute;
  display: none;
  transition: all 0.3s ease-out 0s;
}

.projectImg {
  width: 280px;
  height: 160px;
  background-size: 100%;
  margin: 20px 0;
  border-radius: 4px;
}

.projectTitle {
  color: $TextTitle-color;
  font-size: $TextTitle-size;
  font-weight: 400px;
  margin-bottom: 8px;
}

.projectText {
  color: $textP;
  font-size: 14px;
}

</style>



