<template>
  <div class="nav" :class="{'navBarShadow':scrollType}">
    <div class="container" :class="{'containerLitter':scrollType}">
      <div class="nav-left-box">
        <a class="logo"></a>
        <ul class="text-link">
          <a href="javascript:;" @click="openHome">
            <li :style="active==1?'color:#fff;':''">首页</li>
          </a>
          <a href="javascript:;" @click="openProject">
            <li :style="active==2?'color:#fff;':''">项目</li>
          </a>
          <a href="javascript:;">
            <li :style="active==3?'color:#fff;':''">素材</li>
          </a>
        </ul>
      </div>
      <div class="nav-right-box">
        <a href="javascript:;" @click="postWeChatCenterDialogVisibleTrue">
          <div class="wechat chat"></div>
        </a>
        <a href="javascript:;" @click="qqIM">
          <div class="qq chat"></div>
        </a>
        <a href="javascript:;" @click="sendEmail">
          <div class="email chat"></div>
        </a>
      </div>
    </div>
  </div>
</template>
<script type="text/javascript">
export default {
  //该页面的控制数据
  data() {
    return {
      scrollType: false,
    }
  },
  //父控件传过来的参数
  props: {
    bodyWidth: {
      type: Number,
      default: 1440
    },
  },
  computed: {
    active() {
      return this.$store.getters.getNavBarActive;
    }
  },
  //监听函数
  wacth: {

  },
  //进入页面执行的函数
  mounted() {
    this.$store.commit('updateNavBarActive', '1');
    this.$nextTick(() => {
      window.addEventListener('scroll', this.onScroll)
    });
  },
  //定义函数
  methods: {
    openHome() {
      this.$store.commit('updateNavBarActive', '1');
      this.$router.push({ path: '/' });
    },
    openProject() {
      this.$store.commit('updateNavBarActive', '2');
    },
    //滚轮缩小事件
    onScroll() {
      let scrolled = document.documentElement.scrollTop || document.body.scrollTop
      if (scrolled >= 80) {
        this.scrollType = true;
      } else {
        this.scrollType = false;
      }
    },
    //打开微信二维码
    postWeChatCenterDialogVisibleTrue() {
      this.$store.commit('updateCenterDialogVisible', true)
      this.$store.commit('updatemodalStatus', "wechat")
    },
    //QQ聊天
    qqIM() {
      window.open('http://wpa.qq.com/msgrd?v=3&uin=197299278&site=qq&menu=yes', '_blank', 'height=502, width=644,toolbar=no,scrollbars=no,menubar=no,status=no');
    },
    //发送邮件
    sendEmail() {
      window.open('mailto:197299278@qq.com', '_blank', 'height=502, width=644,toolbar=no,scrollbars=no,menubar=no,status=no');
    },
  },
  //增加控件
  components: {

  }
}

</script>
<style scoped lang="scss">
@import '../assets/sass/public.scss';
.nav {
  background: $navBarBg;
  width: 100%;
  height: 80px;
  position: fixed;
  z-index: 10;
}

.navBarShadow {
  transition: all 0.3s ease-out 0s;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
  height: 60px;
}

.container {
  max-width: 1180px;
  min-width: 1024px;
  overflow: hidden;
  margin: 0 auto;
  height: 80px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.containerLitter {
  height: 60px;
  transition: all 0.3s ease-out 0s;
}

.nav-left-box {
  display: flex;
  align-items: center;
}

.logo {
  float: left;
  width: 134px;
  height: 40px;
  background: url(../assets/logo.png);
  background-size: 100%;
}

.text-link {
  margin: 0;
  float: left;
  margin-left: 100px;
}

.text-link li {
  color: #8492A6;
  list-style-type: none;
  float: left;
  font-size: 18px;
  margin-right: 100px;
  font-weight: 350;
  transition: all 0.3s ease-out 0s;
}

.text-link li:hover {
  color: #fff;
  transition: all 0.3s ease-out 0s;
}

.nav-right-box {
  display: flex;
}

.chat {
  width: 40px;
  height: 40px;
  margin-left: 20px;
  border-radius: 20px;
  border: 1px solid #fff;
  transition: all 0.3s ease-out 0s;
  box-sizing: border-box;
}

.chat:hover {
  transition: all 0.3s ease-out 0s;
  box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.5);
}

.wechat {
  background-image: url(../assets/img/wechat.png);
  background-size: 100%;
}

.qq {
  background-image: url(../assets/img/qq.png);
  background-size: 100%;
}

.email {
  background-image: url(../assets/img/email.png);
  background-size: 100%;
}

</style>



