import Vue from 'vue';
import Vuex from 'vuex';
import actions from './api';

Vue.use(Vuex);

export default new Vuex.Store({
  actions,
  state: {
    //首页弹窗
    centerDialogVisible: false,
    //模态框类型
    modalStatus: "",
    //顶部栏选择页面
    navBarActive:"",
    //列表详情projectId
    projectId:"",
  },
  getters: {
    getCenterDialogVisible(state) {
      return state.centerDialogVisible;
    },
    getModalStatus(state) {
      return state.modalStatus;
    },
    getNavBarActive(state) {
      return state.navBarActive;
    },
  },
  mutations: {
    //首页弹窗状态更改
    updateCenterDialogVisible(state, centerDialogVisible) {
      state.centerDialogVisible = centerDialogVisible;
    },
    //首页模态框类型
    updatemodalStatus(state, modalStatus) {
      state.modalStatus = modalStatus;
    },
    //顶部栏选择页面
    updateNavBarActive(state, navBarActive) {
      state.navBarActive = navBarActive;
    },
    //列表详情projectId
    updateProjectId(state, projectId) {
      state.projectId = projectId;
    },
  }
});



