<template>
  <div class="detailsBody">
    <div class="detailsHeader">
      <div class="detailsTop">
        <div class="litterImg" :style="'background-image: url('+litterImg+');'"></div>
        <div class="detailsTextBox">
          <div class="textTitle">{{projectData.projectTitle}}</div>
          <div class="dateAndCopyright">{{projectData.projectDate}} ©{{projectData.projectCopyright}}</div>
          <div class="textSynopsis">{{projectData.projectSynopsis}}</div>
        </div>
        <a href="javascript:;">
          <div class="downloadBtn" v-if="projectData.projectCopyright != 'Windy'">
            <div class="btnIcon"></div>
            <div class="btnText">本地下载</div>
          </div>
        </a>
      </div>
    </div>
    <div class="details">
      <!-- <div><img class="detailsPic" :src="'http://127.0.0.1:5000/static/'+ projectData.projectDetail"></div> -->
      <div v-html="projectData.projectDetail"></div>
      <div class="text">如果您觉得本狗做的不错，或者资源对您有用，欢迎任意金额打赏支持</div>
      <div style="display: flex; margin: auto; width: 180px;">
        <a href="javascript:;" @click="showPayBox=!showPayBox">
          <div class="payBtn">赏</div>
        </a>
        <a href="javascript:;" @click="refish">
          <div class="pinlun">评论</div>
        </a>
      </div>
      <transition name="payBox">
        <div v-if="showPayBox">
          <div class="payBox"></div>
          <div class="payText">打赏将支持作者提供更好的服务与更优质的资源</div>
        </div>
      </transition>
      <div id="SOHUCS" :sid="projectId"></div>
    </div>
  </div>
</template>
<script type="text/javascript">
import $ from "jquery"
import changyan from "@/assets/js/changyan.js"
export default {
  //该页面的控制数据
  data() {
    return {
      //首页列表中传来的projectId参数
      // projectId: this.$route.params.projectId,
      projectId: localStorage.getItem("projectId"),

      //是否显示打赏二维码
      showPayBox: false,
      //项目使用的数据
      projectData: "",
      litterImg:"",
      //项目数据
      baobei201708: {
        projectTitle: "香港报贝2.0商务移动报销APP",
        projectPic: "baobei201708.png",
        projectDate: "2017年4月",
        projectCopyright: "Windy",
        projectSynopsis: "这是在2015年通过朋友介绍所获得的一次与香港同胞合作的项目，当时的香港互联网并不发达，移动互联网刚刚兴起，于是一位与时俱进的香港老板来到大陆寻求移动互联网的契机。在这次机会中，我有幸与她合作，完成当时报贝的1.0版本。这是在2年后，这位老朋友再次找到我，让我给当时的报贝1.0设计升级迭代版本。于是有了下面的报贝2.0。",
        projectImg: "baobei201708.png",
      },
      jidong201801: {
        projectTitle: "广州极动MES工业PAD移动端",
        projectPic: "jidong201801.png",
        projectDate: "2018年1月",
        projectCopyright: "Windy",
        projectSynopsis: "该项目为任职东风设计院，与广州极动焊接技术有限公司所接的一个生产执行系统的一个外包项目。该项目的需求为将已经成熟软件MES，作为平台技术，通过数据接口调用，二次开发工人的手持移动端。由于移动端上需求只需要对生产报工，质检，设备故障报警，以及工时统计查询等功能，只需要对数据库进行增删改查的调用。所以，在不需要原生系统开发的情况下，我选用的vue.js作为webapp的开发框架。针对工厂需求，从原型到UI设计，再到前端代码实现，最后与平台对接口，到一个完整的APP，其中整个项目都由我一人包办。",
        projectImg: "jidong201801.png",
      },
      xunyi201711: {
        projectTitle: "寻医问药APP",
        projectPic: "xunyi201711.png",
        projectDate: "2017年11月",
        projectCopyright: "Windy",
        projectSynopsis: "这是一款为了方便用户，直接就能使用语音输入控制的医院挂号APP。用户仅仅只需要点击按钮录音，APP会根据用户的语音自动匹配响应的医生给用户选择挂号。",
        projectImg: "xunyi201711.png",
      },
      waimai201707: {
        projectTitle: "报贝外卖",
        projectPic: "waimai201707.png",
        projectDate: "2017年07月",
        projectCopyright: "Windy",
        projectSynopsis: "由于报贝2.0的业务扩展需求，需要将外卖的业务扩展至报贝的模块之中，特此，设计了这款外卖的APP。该APP分为商户版和用户版。商户登录商户版进行产品的上架管理与收银。用户登录用户版本进行点餐与外卖。红色为商户版，橙色为用户版。",
        projectImg: "waimai201707.png",
      },
      xiaodianlun201705: {
        projectTitle: "小电轮-共享电动车",
        projectPic: "xiaodianlun201705.png",
        projectDate: "2017年05月",
        projectCopyright: "Windy",
        projectSynopsis: "该项目为微信小程序共享电动车，从LOGO设计到整个小程序的界面，以及共享电车的车架设计都经过了几次的改版和头脑风暴。该项目暂未上线。",
        projectImg: "xiaodianlun201705.png",
      },
      mss201704: {
        projectTitle: "东风设计研究院智能管理系统MSS",
        projectPic: "mss201704.png",
        projectDate: "2017年04月",
        projectCopyright: "Windy",
        projectSynopsis: "该项目目前与东风本田与郑州日产设备管理进行使用中。",
        projectImg: "mss201704.png",
      },
      mesapp201703: {
        projectTitle: "东风设计研究院生产执行系统APP",
        projectPic: "mesapp201703.png",
        projectDate: "2017年03月",
        projectCopyright: "Windy",
        projectSynopsis: "该项目目前与东风设计研究院装备研发中心（东风涂装）使用中，从原型到设计到程序代码到平台发布，全部由我主导并完成。",
        projectImg: "mesapp201703.png",
      },
      one2pay201709: {
        projectTitle: "香港one2payPOS机界面",
        projectPic: "one2pay201709.png",
        projectDate: "2017年09月",
        projectCopyright: "Windy",
        projectSynopsis: "为香港报贝POS机界面设计。",
        projectImg: "one2pay201709.png",
      },
    }
  },
  created() {
    //this.showData();
    this.$nextTick(() => {
      this.changyan();
    });
  },
  //父控件传过来的参数
  props: {

  },
  //监听函数
  wacth: {

  },
  //进入页面执行的函数
  mounted() {
    //传入navBar的选择状态
    this.$store.commit('updateNavBarActive', '');
    document.documentElement.scrollTop = 0;
    //this.changyan();
    this.getDesignData();
  },
  //定义函数
  methods: {
    refish() {
      location.reload();
    },
    changyan() {
      window.changyan.api.config({
        appid: 'cyt3crepq',
        conf: 'prod_a13b2e3f57b739f379a9d121e340340d'
      });
    },
    //请求设计详情
    getDesignData() {
      //this.params.touser = this.$store.state.userNameList;
      let params = this.projectId;
      this.$store.dispatch('getDesignData', params).then((response) => {
        let res = response.data;
        if (response.statusText === "OK" && response.status === 200) {
          //console.log(res);
          this.projectData = res;
          this.litterImg = "/static/uploads/" + this.projectData.projectPic;
        } else {
          alert("网络错误")
        }
      }).catch((err) => {
        console.error(err);
      });
    },
    //字符串转HTML
    encodeHtml(str) {
      let encodedStr = "";
      if (str == "") return encodedStr;
      else {
        for (let i = 0; i < str.length; i++) {
          encodedStr += "&#" + str.substring(i, i + 1).charCodeAt().toString(10) + ";";
        }
      }
      return encodedStr;
    },
  },
  //增加控件
  components: {

  }
}

</script>
<style scoped lang="scss">
@import '../assets/sass/public.scss';
.detailsBody {
  padding-top: 80px;
  background-color: $bgColorbai;
  padding-bottom: 20px;
}

.detailsHeader {
  background-color: #fff;
  width: 100%;
}

.detailsTop {
  min-width: 1024px;
  width: 1180px;
  height: 240px;
  margin: auto;
  display: flex;
  align-items: center;
  position: relative;
}

.litterImg {
  width: 300px;
  height: 200px;
  border-radius: 4px;
  background-color: $bgColorLight;
  background-size: 100%;
}

.detailsTextBox {
  height: 200px;
  margin-left: 30px;
  width: 850px;
  min-width: 400px;
}

.textTitle {
  font-size: 24px;
  color: $bigTitle;
}

.dateAndCopyright {
  font-size: 16px;
  color: $textP;
  margin: 10px 0;
}

.textSynopsis {
  font-size: 16px;
  color: $TextTitle-color;
  font-weight: 400;
}

.downloadBtn {
  width: 160px;
  height: 44px;
  background-color: $blue;
  border-radius: 4px;
  position: absolute;
  top: 20px;
  right: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease-out 0s;
}

.downloadBtn:hover {
  background-color: #629DFF;
  transition: all 0.3s ease-out 0s;
}

.btnIcon {
  width: 20px;
  height: 20px;
  background-image: url(../assets/img/downloadIcon.png);
  background-size: 100%;
}

.btnText {
  color: #fff;
  font-size: 16px;
  margin-left: 5px;
}

.details {
  width: 1260px;
  margin: 20px auto 0;
  padding: 40px 0;
  background-color: #fff;
  border-radius: 4px;
  text-align: center;
}

.detailsPic {
  width: 1180px;
  margin: auto;
  background-color: $bgColorLight;
  display: block;
}

.text {
  font-size: 16px;
  color: $textP;
  margin: 30px 0;
}

.payBtn {
  width: 80px;
  height: 80px;
  color: #fff;
  background: #FFB53D;
  font-size: 24px;
  border-radius: 44px;
  text-align: center;
  line-height: 76px;
  transition: all 0.3s ease-out 0s; // background-image: url(../assets/img/payBtn.png);
  // background-size: 100%;
  margin: auto;
}

.pinlun {
  width: 80px;
  height: 80px;
  color: #fff;
  background: #458CFF;
  font-size: 18px;
  border-radius: 44px;
  text-align: center;
  line-height: 76px;
  transition: all 0.3s ease-out 0s; // background-image: url(../assets/img/payBtn.png);
  // background-size: 100%;
  margin-left: 20px;
}

.pinlun:hover {
  background: #186FFF;
  transition: all 0.3s ease-out 0s;
}

.payBox {
  width: 360px;
  height: 220px;
  background-size: 100%;
  background-image: url(../assets/img/payMoney.png);
  margin: 40px auto 10px;
}

.payBtn:hover {
  background: #F7A216;
  transition: all 0.3s ease-out 0s;
}

.payText {
  font-size: 16px;
  color: $textP;
  margin: 20px 0;
}

.payBox-enter-active {
  transition: all .5s ease .2s;
}

.payBox-leave-active {
  transition: all .5s cubic-bezier(1.0, 2, 0.8, 1.0);
  animation: reverse;
}

.payBox-enter,
.payBox-leave-to
/* .slide-fade-leave-active for below version 2.1.8 */

{
  transform: translateX(150px);
  opacity: 0;
}

@keyframes payBox-in {
  0% {
    opacity: 0%;
  }
  10% {
    opacity: 10%;
  }
  20% {
    opacity: 20%;
  }
  50% {
    opacity: 50%;
  }
  100% {
    opacity: 100%;
  }
}

</style>



