import Vue from 'vue';
import axios from 'axios';
import store from './store';
import router from '../router';

export default {
  //请求设计分类
  getDesignCate({ commit, state }, params) {
    return axios({
      url: 'api/design_cate',
      method: 'get',
      params,
    });
  },
  //请求设计列表
  getDesignList({ commit, state }, params) {
    return axios({
      url: 'api/design_list',
      method: 'get',
      params,
    });
  },
  //请求设计详情
  getDesignData({ commit, state }, params) {
    return axios({
      url: 'api/design_detail/'+ params,
      method: 'get',
      params,
    });
  }
}



