<style scoped lang="scss">
@import '../assets/sass/public.scss';

.homeBody {
  min-width: 1024px;
  width: 100%;
}

.text-title {
  margin: 40px auto;
  text-align: center;
}

.text-title h2,
p {
  -webkit-margin-before: 0;
  -webkit-margin-after: 0;
}

.text-title h2 {
  font-size: 24px;
  color: $bigTitle;
  margin-bottom: 10px;
  font-weight: 400;
}

.text-title p {
  color: $textP;
  font-weight: 350;
  font-size: 14px;
}

</style>



