import Vue from 'vue'
import Router from 'vue-router'
import home from '@/pages/home'//首页
import designDetails from '@/pages/designDetails'//作品详情

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: home
    },{
      path: '/designDetails',
      name: 'designDetails',
      component: designDetails
    }
  ]
})



